import React from 'react';
import { injectIntl } from 'react-intl';
import Page from 'material-ui-shell/lib/containers/Page/Page';
import Scrollbar from 'material-ui-shell/lib/components/Scrollbar';
import {
  Card,
  CardHeader,
  CardContent,
  Chip
} from '@material-ui/core';

import data from '../../data/msf';

const { primary, secondary } = data.skills;

const renderPrimarySkills = () => {
  return primary.map(skill => {
    return (
      <Chip
        key={skill}
        label={skill}
        color="primary"
        style={{ margin: '2px' }}
      />
    );
  });
}

const renderSecondarySkills = () => {
  return secondary.map(skill => {
    return (
      <Chip
        key={skill}
        label={skill}
        style={{ margin: '2px' }}
      />
    );
  });
}

const SkillsPage = ({ intl }) => {
  return (
    <Page pageTitle={intl.formatMessage({ id: 'skills' })}>
      <Scrollbar
        style={{ height: '100%', width: '100%', display: 'flex', flex: 1 }}
      >
        {/* {intl.formatMessage({ id: 'skills' })} */}
        <Card>
          <CardHeader
            title="Primary Skills"
          />
          <CardContent>
            {renderPrimarySkills()}
          </CardContent>
        </Card>
        <Card>
          <CardHeader
            title="Secondary Skills"
          />
          <CardContent>
            {renderSecondarySkills()}
          </CardContent>
        </Card>
      </Scrollbar>
    </Page>
  );
}

export default injectIntl(SkillsPage);
